body {
   background-color: #f5f6f7;
}

.container {
    width: 100%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5px;

}

.image-card {
    padding: 5px;
    border: 1px solid #000000;
    border-radius: 10px;
    margin: 15px 15px 5px 15px;
    box-shadow: 22px 15px 0px 0px rgba(2,6,22,1);
}

.image {
    width: 100%;
    height: 600px;
    object-fit: fill;
}

.tags {
    text-align: center;
    padding: 20px 0px;
    background: linear-gradient(90deg, rgba(17,17,17,1) 0%, rgba(43,92,224,1) 64%);
    width: 100%;
    border-bottom: 2px solid white;
}

.tag {
    border: none;
    color: #eee;
    margin: 0px 10px;
    background-color: transparent;
    cursor: pointer;
}

.active {
    font-weight: bold;
    border-bottom: 1px solid #eee;
}

@media only screen and (min-width:850px) and (max-width:1200px) {
    .container {
        grid-template-columns: repeat(3, 1fr);
    }

    .image {
        height: 600px;
    }
}

@media only screen and (min-width:450px) and (max-width:850px) {
    .container {
        grid-template-columns: repeat(2, 1fr);
    }

    .image {
        height: 700px;
    }
}

@media only screen and (min-width:0px) and (max-width:450px) {
    .container {
        display: block;
    }

    .image {
        height: 600px;
    }

    .image-card {
        margin: 15px 25px 45px 25px;
    }
}